@import url(https://cdnjs.cloudflare.com/ajax/libs/octicons/3.5.0/octicons.min.css);
html {
  height: 100%;
  display: flex;
}

body {
  margin: auto;
  background-image: -webkit-linear-gradient(45deg,
      rgba(134, 2, 232, 1) 42%,
      rgba(3, 62, 255, 0.9));
  background-image: linear-gradient(45deg,
      rgba(134, 2, 232, 1) 42%,
      rgba(3, 62, 255, 0.9));
}

.main-container {
  height: 33vh;
  width: 33vw;
  background-color: white;
  border-radius: 5px;
}
.container {
  background-color: white;
  border-radius: 5px;
  padding: 15px;
  text-align: center;
  /* min-width: 45vw; */
}

input {
  max-width: 55px;
}
nav {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  background-color: #0F0E1A;
}

nav>div {
  display: inline-block
}

nav>div.nav-left-container a,
nav>div.nav-right-container a {
  color: white;
}

.nav-left-container {
  margin: 0 25px;
}

.nav-right-container {
  float: right;
  margin: 0 25px;

}

.nav-right-container>a {
  margin: 0 9px;
}
